body, .about-main {
  background: linear-gradient(135deg, #f8fafc 0%, #f4f6fb 100%) !important;
}

.about-main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 1rem;
  background: transparent;
  margin-top: 40px;
}

.about-detailed {
  display: flex;
  gap: 2.5rem;
  align-items: center;
  margin-bottom: 3rem;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 6px 32px rgba(0,0,0,0.08);
  padding: 2.5rem 2rem;
}

.about-detailed__img img {
  width: 340px;
  max-width: 100%;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
}

.about-detailed__content h1 {
  color: #39b54a;
  margin-bottom: 1.2rem;
  font-size: 2.5rem;
  font-weight: 800;
  letter-spacing: 1px;
}

.about-detailed__content p {
  font-size: 1.18rem;
  color: #444;
  line-height: 1.7;
}

.vision-mission {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 3rem;
}

.vision-block, .mission-block {
  background: #fff;
  border-radius: 18px;
  padding: 2.2rem 2rem 2.2rem 1.5rem;
  flex: 1;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  position: relative;
  border-left: 6px solid #39b54a;
}

.vision-block h2, .mission-block h2 {
  color: #39b54a;
  margin-bottom: 0.7rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.vision-block p, .mission-block p {
  color: #444;
  font-size: 1.08rem;
  margin: 0;
}

/* ðŸ”„ Updated section for multiple founders */
.founder-message {
  background: linear-gradient(135deg, #fff7f0 0%, #ffe0cc 100%);
  border-radius: 20px;
  padding: 2.5rem 2rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  margin-bottom: 2rem;
  flex-direction: column;
}

.founders-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  justify-content: space-between;
  margin-top: 1rem;
}

.founder-block {
  flex: 1 1 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.founder-block img {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid #39b54a;
  box-shadow: 0 2px 12px rgba(255,102,0,0.10);
  margin-bottom: 1rem;
}

.founder-content h2 {
  color: #39b54a;
  margin-bottom: 0.7rem;
  font-size: 1.4rem;
  font-weight: 700;
}

.founder-content p {
  font-size: 1.13rem;
  color: #333;
  line-height: 1.7;
  margin-bottom: 0.7rem;
}

.founder-name {
  color: #39b54a;
  font-weight: bold;
  margin-top: 1rem;
  font-size: 1.1rem;
}

.about-registration {
  display: flex;
  gap: 2.5rem;
  align-items: center;
  margin: 3rem 0 0 0;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 6px 32px rgba(0,0,0,0.08);
  padding: 2.5rem 2rem;
}

.about-registration__img img {
  width: 340px;
  max-width: 100%;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
}

.about-registration__content {
  flex: 1;
}

.about-registration__content h2 {
  color: #39b54a;
  margin-bottom: 1.2rem;
  font-size: 2rem;
  font-weight: 800;
  letter-spacing: 1px;
}

.about-registration__content p {
  font-size: 1.18rem;
  color: #444;
  line-height: 1.7;
}

/* ðŸ”„ Responsive Design */
@media (max-width: 900px) {
  .about-detailed, .vision-mission, .about-registration {
    flex-direction: column;
    gap: 1.5rem;
    align-items: flex-start;
    padding: 1.5rem 0.7rem;
  }

  .about-detailed__img img,
  .about-registration__img img {
    width: 100%;
    max-width: 320px;
  }

  .vision-block, .mission-block {
    padding: 1.5rem 1rem 1.5rem 0.7rem;
  }

  .founders-container {
    flex-direction: column;
    align-items: center;
  }

  .founder-block {
    width: 100%;
    max-width: 500px;
  }

  .founder-block img {
    width: 120px;
    height: 120px;
  }
}
